import React from "react";
import Img1 from "../static/anhtheanh.jpg";
import Img2 from "../static/chiphuong.jpg";
import Img3 from "../static/anhduong.jpg";

import { AnimationOnScroll } from "react-animation-on-scroll";

export default function Team() {
  return (
    <div className="bg-[#2B076E] pt-1 xl:pt-10">
      <AnimationOnScroll animateIn="animate__zoomIn" delay={500}>
        <div className="w-[90%] sm:w-[80%] md:w-[40%] mx-auto text-center text-white text-xl py-6 sm:text-3xl">
          i ng CK Capital
        </div>
      </AnimationOnScroll>
      <div className="flex flex-wrap justify-center gap-8 py-10">
        <AnimationOnScroll animateIn="animate__fadeInUp" delay={600}>
          <div className="w-64 text-center">
            <img src={Img1} alt="Member 1" className="w-32 h-32 rounded-full mx-auto" />
            <h3 className="text-white text-lg mt-4">Th Anh</h3>
            <p className="text-gray-400">Founder</p>
          </div>
        </AnimationOnScroll>
        <AnimationOnScroll animateIn="animate__fadeInUp" delay={700}>
          <div className="w-64 text-center">
            <img src={Img2} alt="Member 2" className="w-32 h-32 rounded-full mx-auto" />
            <h3 className="text-white text-lg mt-4">Qunh Phng</h3>
            <p className="text-gray-400">Manager</p>
          </div>
        </AnimationOnScroll>
        <AnimationOnScroll animateIn="animate__fadeInUp" delay={800}>
          <div className="w-64 text-center">
            <img src={Img3} alt="Member 3" className="w-32 h-32 rounded-full mx-auto" />
            <h3 className="text-white text-lg mt-4">Dng Ocean</h3>
            <p className="text-gray-400">CTO</p>
          </div>
        </AnimationOnScroll>
      </div>
    </div>
  );
}
